@use '../structure/base';
@use "../structure/boostrap";


.auth-page-wrapper{
    
    .auth-page-content{
        //padding-bottom: 60px;
        position: relative;
        z-index: 2;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto;
        height: 90vh;
        .auth-card{
            @extend %glass1;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            .auth-logo{
                flex-direction: row;
                justify-content: center;
                .logo{
                    height: 36px;
                    width: auto;
                    margin-right: 10px;
                }
            }
            .dummy-icon{
                .icon{
                    color: transparent;
                }
            }
        }
    }
}

.auth-one-bg-position{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 380px;
    
    @media (max-width: 575.98px) {
        height: 280px;
    }
}

.auth-one-bg{
    //background-image: url("../../images/fence4.jpg");
    background-position: center;
    background-size: cover;
    background-color: var(--spanish-green-faint);

    .bg-overlay{
        background: linear-gradient(90deg, rgba(145,224,0,1) 0%, rgba(18,16,14,1) 100%);
        opacity: 0.9;
    }

    .shape{
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: 1;
        pointer-events: none;
        >svg {
            width: 100%;
            height: auto;
            fill: var(--white);
        }
    }
}
